.title_img {
max-width: 700px;
margin: 30px auto;
background-color: white;
padding: 10px 20px;
box-shadow: 0 0 10px 1px black;
img {
    width:100%;
}
}
.describe_image {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: space-between;
    padding: 0 30px;
    img{
        height: 35px;
        width:35px;
    }
   div {
    align-items: center;
    display: flex;
    img {
        margin-right:10px;
    }
   }
     
}