.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.6);
    position:fixed;
    top:0;
    left:0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity:0;
    pointer-events: none;
    transition: all 0.5s;
}
.modal.active {
opacity:1;
pointer-events: all;

}
.modal_content {
    width:80vw;
    padding:20px;
    border-radius: 12px;
    background-color: white;
    width: 50vw;
    transform: scale(0.5);
    transition: all 0.5s;
}
.modal_content.active {
    transform: scale(1);
}